import { Component, OnInit, OnDestroy } from '@angular/core';
import { MoviesService } from './services/movies.service';
import { Subscription } from 'rxjs';
import { ActivatedRoute, Params, Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {

  showSpinner1; // = false;

  private spinnerSub: Subscription;

  constructor(private movieService: MoviesService, private route: ActivatedRoute, private router: Router) {}

  ngOnInit() {
    // console.log('app component initialized');
    this.movieService.pageNumber = 1;
     // this.movieService.populateMovies(false);
        // subscribe to spinnerSubject<boolean>
        this.spinnerSub = this.movieService.spinnerSubject.subscribe(
          (bool: boolean) => {
            // this.showSpinner1 = bool;
            // console.log(this.showSpinner1);
          }
        );


  }

  ngOnDestroy() {
    this.spinnerSub.unsubscribe();
  }

}
