import {
  Component,
  OnInit,
  AfterViewInit,
  OnDestroy,
  HostListener,
  Input
} from '@angular/core';
import { MoviesService } from 'src/app/services/movies.service';
import { Subscription } from 'rxjs';
import { Movie } from 'src/app/models/movie.model';
import { ActivatedRoute } from '@angular/router';
import { tap } from 'rxjs/operators';

@Component({
  selector: 'app-movie-list',
  templateUrl: './movie-list.component.html',
  styleUrls: ['./movie-list.component.css']
})
export class MovieListComponent implements OnInit, OnDestroy, AfterViewInit {
  movieArray: any;
  movies;
  noMoviesinDB;
  showSpinner; // = true;

  private movieArraySubscription: Subscription;
  private spinnerSub: Subscription;

  constructor(private movieService: MoviesService, private route: ActivatedRoute) {}

  ngOnInit() {
    this.movieArraySubscription = this.movieService.movieArrayUpdated.subscribe(
      (movies: Movie[]) => {
        this.movieArray = movies;
      }
    );
    this.movieService.readmovieArray();

    this.movieService.emptyArraySubject.subscribe(
      (data: boolean) => {
        this.noMoviesinDB = data;
      }
    );

    this.spinnerSub  = this.movieService.spinnerSubject.subscribe(
      (data: boolean) => {
        this.showSpinner = data;
        // if (data) {
        //   this.movieService.emptyArraySubject.next(false);
        //   console.log('PRINT WORDS LIKE LOADING WHILE SPIN IS TRUE');
        // }
      }
    );
    // this.movieService.spinnerSubject.next(true);
    // this.movieArray = this.route.snapshot.data.movieList;
  }

  ngAfterViewInit() {
    // once the list from the db has been projected onto the view (because its asyncronous)
    console.log(this.movieService.pageYOffset);
    scrollTo(0, this.movieService.pageYOffset); // scroll to previous pageYOffset using stored in-memory pageYOffset

  }

  ngOnDestroy() {
    if (this.movieArray) {
      this.movieArraySubscription.unsubscribe();
      // this.spinnerSub.unsubscribe();
    }
    if (this.spinnerSub) {
      this.spinnerSub.unsubscribe();
    }
  }

}
