import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { MoviesService } from 'src/app/services/movies.service';
import { Movie } from 'src/app/models/movie.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-movie-item',
  templateUrl: './movie-item.component.html',
  styleUrls: ['./movie-item.component.css']
})
export class MovieItemComponent implements OnInit {
  @Input() movie: Movie;
  @Input() index: number;
  searchBarTitle: String;
  fixedDescription;
  fixedTitle;

  defaultImg = `data:image
  /svg+xml;base64,
  PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyNTAgMjUwIj4KICAgIDxwYXRoIGZpbGw9IiNERDA
  wMzEiIGQ9Ik0xMjUgMzBMMzEuOSA2My4ybDE0LjIgMTIzLjFMMTI1IDIzMGw3OC45LTQzLjcgMTQuMi0xMjMuMXoiIC8+CiAgICA8cGF0aCBmaW
  xsPSIjQzMwMDJGIiBkPSJNMTI1IDMwdjIyLjItLjFWMjMwbDc4LjktNDMuNyAxNC4yLTEyMy4xTDEyNSAzMHoiIC8+CiAgICA8cGF0aCAgZmlsb
  D0iI0ZGRkZGRiIgZD0iTTEyNSA1Mi4xTDY2LjggMTgyLjZoMjEuN2wxMS43LTI5LjJoNDkuNGwxMS43IDI5LjJIMTgzTDEyNSA1Mi4xem0xNyA4
  My4zaC0zNGwxNy00MC45IDE3IDQwLjl6IiAvPgogIDwvc3ZnPg==`;

  constructor(private movieService: MoviesService, private route: Router) {}

  ngOnInit() {

    if (this.movie.title.length > 50) {
      this.fixedTitle = this.movie.title.substring(0, 38) + '...';
    } else {
      this.fixedTitle = this.movie.title;
    }

    if (this.movie.description.length > 150) {
      this.fixedDescription = this.movie.description.substring(0, 150) + '...';
    } else {
      this.fixedDescription = this.movie.description;
    }
  }

  savePageYOffset() {
    // Saves the previous pages' (movielist.component) Y offset and stores it into in-memory pageYOffset
    this.movieService.pageYOffset = window.pageYOffset;
    this.convertSpaceToUnderscore(this.movie.title);
    this.route.navigate(['/movies', this.searchBarTitle]);

    this.movieService.currentMovieIndex = this.index; // saves index of currently selected movieItem in-memory
  }

  convertSpaceToUnderscore(title: String) {
    for (const char in title) {
      if (title[char] === ' ') {
        title = title.replace(' ', '_');
      }
    }
    // console.log(title.toLowerCase());
    this.searchBarTitle = title.toLowerCase();
  }
}
